<template>
    <div class="medicineManager">
        <div class="title-box">
            > 
        </div>
        <div class="opration-buttons-box">
            <el-form :inline="true" label-position="left">
                <el-form-item label="">
                    <el-input placeholder="" v-model="searchForm.name"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select placeholder="" v-model="searchForm.isOTC">
                        <el-option label="" :value="true"></el-option>
                        <el-option label="" :value="false"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-input placeholder="" v-model="searchForm.price"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select placeholder="" v-model="searchForm.status">
                        <el-option label="" :value="0"></el-option>
                        <el-option label="" :value="1"></el-option>
                        <el-option label="" :value="2"></el-option>
                        <el-option label="" :value="3"></el-option>
                        <el-option label="" :value="4"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-input placeholder="" v-model="searchForm.desc"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" :loading="searchLoading" @click="seachSubmit"></el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="centerDialogVisible = true"></el-button>
                </el-form-item>
            </el-form>
        </div>
        <el-table :data="tableData" border stripe height="450" style="width: 100%">
            <el-table-column prop="name" label="">
            </el-table-column>
            <el-table-column prop="isOTC" label="">
            </el-table-column>
            <el-table-column prop="price" label="">
            </el-table-column>
            <el-table-column prop="status" label="">
            </el-table-column>
            <el-table-column prop="desc" label="">
            </el-table-column>
            <el-table-column fixed="right" label="" width="100">
                <template slot-scope="scope">
                    <el-button type="text" size="small"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination background layout="prev, pager, next" :total="1000" class="pagination-box" @current-change="currentPageFn"></el-pagination>
        <el-dialog title="" :visible.sync="centerDialogVisible" width="50%" center>
            <el-form ref="form" :model="addForm" label-width="80px">
                <el-form-item label="">
                    <el-input v-model="addForm.name" clearable></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select placeholder="" v-model="addForm.isOTC">
                        <el-option label="" :value="true"></el-option>
                        <el-option label="" :value="false"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-input placeholder="" v-model="addForm.price" clearable></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select placeholder="" v-model="addForm.status">
                        <el-option label="" :value="0"></el-option>
                        <el-option label="" :value="1"></el-option>
                        <el-option label="" :value="2"></el-option>
                        <el-option label="" :value="3"></el-option>
                        <el-option label="" :value="4"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="textarea" v-model="addForm.desc" clearable></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="cancelAddSubmit"> </el-button>
                <el-button type="primary" @click="addSubmit" :loading="addLoading"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
export default {
    name: 'MedicineManager',
    data () {
        return {
            searchLoading: false,
            centerDialogVisible: false,
            addLoading: false,
            addForm: {
                name: '',
                isOTC: false,
                price: 0,
                status: 0,
                desc: ''
            },
            searchForm: {
                name: '',
                isOTC: false,
                price: 0,
                status: 0,
                desc: ''
            },
            tableData: [
                {
                    name: '',
                    isOTC: false,
                    price: 0,
                    status: 0,
                    desc: ''
                }, {
                    name: '',
                    isOTC: false,
                    price: 0,
                    status: 0,
                    desc: ''
                }, {
                    name: '',
                    isOTC: false,
                    price: 0,
                    status: 0,
                    desc: ''
                }, {
                    name: '',
                    isOTC: false,
                    price: 0,
                    status: 0,
                    desc: ''
                }, {
                    name: '',
                    isOTC: false,
                    price: 0,
                    status: 0,
                    desc: ''
                }, {
                    name: '',
                    isOTC: false,
                    price: 0,
                    status: 0,
                    desc: ''
                }, {
                    name: '',
                    isOTC: false,
                    price: 0,
                    status: 0,
                    desc: ''
                }, {
                    name: '',
                    isOTC: false,
                    price: 0,
                    status: 0,
                    desc: ''
                }, {
                    name: '',
                    isOTC: false,
                    price: 0,
                    status: 0,
                    desc: ''
                }, {
                    name: '',
                    isOTC: false,
                    price: 0,
                    status: 0,
                    desc: ''
                }, {
                    name: '',
                    isOTC: false,
                    price: 0,
                    status: 0,
                    desc: ''
                }, {
                    name: '',
                    isOTC: false,
                    price: 0,
                    status: 0,
                    desc: ''
                }
            ]
        }
    },
    methods: {
        seachSubmit () {
            this.searchLoading = true
            console.log(this.searchForm)
            let that = this
            setTimeout(function () {
                that.searchLoading = false
            }, 2000)
        },
        currentPageFn (val) {
            console.log(`${val}`)
        },
        addSubmit () {
            this.addLoading = true
            console.log(this.addForm)
            let that = this
            setTimeout(function () {
                that.addLoading = false
                that.centerDialogVisible = false
            }, 2000)
        },
        cancelAddSubmit () {
            this.centerDialogVisible = false
            this.addForm.username = ''
            this.addForm.department = ''
            this.addForm.title = ''
        }
    }
}
</script>




