<template>
    <div class="pharmacyOperation">
        <div class="title-box">
            > 
        </div>
        <div class="opration-buttons-box">
            <el-form :inline="true">
                <el-form-item label="">
                    <el-input placeholder="" v-model="searchForm.name"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input placeholder="" v-model="searchForm.department"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-radio-group v-model="searchForm.visitDateStage">
                        <el-radio label="" value="0"></el-radio>
                        <el-radio label="" value="1"></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="">
                    <el-date-picker type="date" placeholder="" v-model="searchForm.visitDate" style="width: 100%;"></el-date-picker>
                </el-form-item>
                <el-form-item label="">
                    <el-switch v-model="searchForm.status">
                    </el-switch>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" :loading="searchLoading" @click="seachSubmit"></el-button>
                </el-form-item>
            </el-form>
        </div>
        <el-table :data="tableData" border stripe height="450" style="width: 100%" v-loading="searchLoading">
            <el-table-column prop="createTime" label="">
            </el-table-column>
            <el-table-column prop="name" label="">
            </el-table-column>
            <el-table-column prop="visitDate" label="">
            </el-table-column>
            <el-table-column prop="visitDateStage" label="">
            </el-table-column>
            <el-table-column prop="type" label="">
            </el-table-column>
            <el-table-column fixed="right" label="" width="100">
                <template slot-scope="scope">
                    <el-button type="text" size="small"  @click="editById"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination background layout="prev, pager, next" :total="1000" class="pagination-box" @current-change="currentPageFn"></el-pagination>
        <el-dialog title="" :visible.sync="centerDialogVisible" width="60%" center :show-close="false">
            <el-table :data="medicineTableData" border stripe height="400" style="width: 100%" >
                <el-table-column type="index" width="50">
                </el-table-column>
                <el-table-column prop="createTime" label="">
                </el-table-column>
                <el-table-column prop="createTime" label="">
                </el-table-column>
                <el-table-column prop="createTime" label="">
                </el-table-column>
            </el-table>
            <span slot="footer" class="dialog-footer">
                <el-button @click="centerDialogVisible = false" :loading="overLoading"> </el-button>
                <el-button type="primary" @click="overSubmit" :loading="overLoading"></el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
export default {
    name: 'PharmacyOperation',
    data () {
        return {
            overLoading: false,
            searchLoading: false,
            centerDialogVisible: false,
            searchForm: {
                name: '',
                department: '',
                visitDate: '',
                visitDateStage: ''
            },
            tableData: [
                {
                    createTime: '',
                    name: '',
                    visitDate: '',
                    visitDateStage: '',
                    type: ''
                }, {
                    createTime: '',
                    name: '',
                    visitDate: '',
                    visitDateStage: '',
                    type: ''
                }, {
                    createTime: '',
                    name: '',
                    visitDate: '',
                    visitDateStage: '',
                    type: ''
                }, {
                    createTime: '',
                    name: '',
                    visitDate: '',
                    visitDateStage: '',
                    type: ''
                }, {
                    createTime: '',
                    name: '',
                    visitDate: '',
                    visitDateStage: '',
                    type: ''
                }, {
                    createTime: '',
                    name: '',
                    visitDate: '',
                    visitDateStage: '',
                    type: ''
                }
            ],
            medicineTableData: [
                {
                    name: '',
                    isOTC: true,
                    price: '',
                    desc: ''
                }, {
                    name: '',
                    isOTC: true,
                    price: '',
                    desc: ''
                }, {
                    name: '',
                    isOTC: true,
                    price: '',
                    desc: ''
                }, {
                    name: '',
                    isOTC: true,
                    price: '',
                    desc: ''
                }, {
                    name: '',
                    isOTC: true,
                    price: '',
                    desc: ''
                }, {
                    name: '',
                    isOTC: true,
                    price: '',
                    desc: ''
                }
            ]
        }
    },
    methods: {
        seachSubmit () {
            this.searchLoading = true
            let that = this
            setTimeout(function () {
                that.searchLoading = false
                that.centerDialogVisible = false
            }, 1000)
        },
        currentPageFn (page) {
            console.log(`${page}`)
        },
        editById () {
            this.centerDialogVisible = true
        },
        overSubmit () {
            let that = this
            this.$confirm('')
            .then(_ => {
                that.overLoading = true
                // done()
                setTimeout(function () {
                    this.overLoading = false
                    that.centerDialogVisible = false
                }, 1000)
            })
            .catch(_ => {
                this.overLoading = false
            })
        }
    }

}
</script>




