<template>
    <div class="registerListOperation">
        <div class="title-box">
            > 
        </div>
        <div class="opration-buttons-box">
            <el-form :inline="true">
                <el-form-item label="">
                    <el-input placeholder="" v-model="searchForm.name"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input placeholder="" v-model="searchForm.department"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input placeholder="" v-model="searchForm.doctorId"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-radio-group v-model="searchForm.visitDateStage">
                        <el-radio label="" value="0"></el-radio>
                        <el-radio label="" value="1"></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="">
                    <el-col :span="11">
                        <el-date-picker type="date" placeholder="" v-model="searchForm.visitDate" style="width: 100%;"></el-date-picker>
                    </el-col>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" :loading="searchLoading" @click="seachSubmit"></el-button>
                </el-form-item>
            </el-form>
        </div>
        <el-table :data="tableData" border stripe height="450" style="width: 100%" v-loading="searchLoading">
            <el-table-column prop="createTime" label="">
            </el-table-column>
            <el-table-column prop="name" label="">
            </el-table-column>
            <el-table-column prop="cardId" label="">
            </el-table-column>
            <el-table-column prop="socialSecurityId" label="">
            </el-table-column>
            <el-table-column prop="department" label="">
            </el-table-column>
            <el-table-column prop="doctorId" label="">
            </el-table-column>
            <el-table-column prop="address" label="">
            </el-table-column>
            <el-table-column prop="mobile" label="">
            </el-table-column>
            <el-table-column prop="visitDate" label="">
            </el-table-column>
            <el-table-column prop="visitDateStage" label="">
            </el-table-column>
            <el-table-column prop="type" label="">
            </el-table-column>
            <el-table-column prop="price" label="">
            </el-table-column>
            <el-table-column fixed="right" label="" width="100">
                <template slot-scope="scope">
                    <el-button type="text" size="small"  @click="editById"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination background layout="prev, pager, next" :total="1000" class="pagination-box" @current-change="currentPageFn"></el-pagination>
        <el-dialog title="" :visible.sync="centerDialogVisible" width="50%" center>
            <el-form ref="form" :model="addForm" label-width="80px">
                <el-form-item label="">
                    <el-input v-model="addForm.name" disabled></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="addForm.cardId" disabled></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="addForm.socialSecurityId" disabled></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="addForm.mobile" disabled></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="addForm.address" disabled></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="addForm.departmentId" disabled></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="addForm.doctorId" disabled></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="addForm.visitDate" disabled></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="addForm.visitDateStage" disabled></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="addForm.type" disabled></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="addForm.price" disabled></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="addForm.desc" disabled></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="cancelAddSubmit"> </el-button>
                <el-button type="primary" @click="addSubmit" :loading="addLoading"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
export default {
    name: 'RegisterListOperation',
    data () {
        return {
            addLoading: false,
            searchLoading: false,
            centerDialogVisible: false,
            searchForm: {
                name: '',
                department: '',
                doctorId: '',
                visitDate: '',
                visitDateStage: ''
            },
            addForm: {
                name: '',
                cardId: '',
                socialSecurityId: '',
                mobile: '',
                address: '',
                departmentId: '',
                doctorId: '',
                visitDate: '',
                visitDateStage: '',
                type: '',
                price: '',
                desc: ''
            },
            tableData: [
                {
                    createTime: '',
                    name: '',
                    cardId: '',
                    socialSecurityId: '',
                    department: '',
                    doctorId: '',
                    address: '',
                    mobile: '',
                    visitDate: '',
                    visitDateStage: '',
                    type: '',
                    price: ''
                }, {
                    createTime: '',
                    name: '',
                    cardId: '',
                    socialSecurityId: '',
                    department: '',
                    doctorId: '',
                    address: '',
                    mobile: '',
                    visitDate: '',
                    visitDateStage: '',
                    type: '',
                    price: ''
                }, {
                    createTime: '',
                    name: '',
                    cardId: '',
                    socialSecurityId: '',
                    department: '',
                    doctorId: '',
                    address: '',
                    mobile: '',
                    visitDate: '',
                    visitDateStage: '',
                    type: '',
                    price: ''
                }, {
                    createTime: '',
                    name: '',
                    cardId: '',
                    socialSecurityId: '',
                    department: '',
                    doctorId: '',
                    address: '',
                    mobile: '',
                    visitDate: '',
                    visitDateStage: '',
                    type: '',
                    price: ''
                }, {
                    createTime: '',
                    name: '',
                    cardId: '',
                    socialSecurityId: '',
                    department: '',
                    doctorId: '',
                    address: '',
                    mobile: '',
                    visitDate: '',
                    visitDateStage: '',
                    type: '',
                    price: ''
                }, {
                    createTime: '',
                    name: '',
                    cardId: '',
                    socialSecurityId: '',
                    department: '',
                    doctorId: '',
                    address: '',
                    mobile: '',
                    visitDate: '',
                    visitDateStage: '',
                    type: '',
                    price: ''
                }
            ]
        }
    },
    methods: {
        seachSubmit () {
            this.searchLoading = true
            let that = this
            setTimeout(function () {
                that.searchLoading = false
                that.centerDialogVisible = false
            }, 2000)
        },
        cancelAddSubmit () {
            this.centerDialogVisible = false
        },
        addSubmit () {
            this.addLoading = true
            let that = this
            setTimeout(function () {
                that.addLoading = false
                that.centerDialogVisible = false
            }, 2000)
        },
        currentPageFn (page) {
            console.log(`${page}`)
        },
        editById () {
            this.centerDialogVisible = true
        }
    }
}
</script>




