<template>
    <div class="userManager">
        <div class="title-box">
            > 
        </div>
        <div class="opration-buttons-box">
            <el-form :inline="true">
                <el-form-item label="">
                    <el-input placeholder="" v-model="searchForm.username"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input placeholder="" v-model="searchForm.department"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select placeholder="" v-model="searchForm.title">
                        <el-option label="" :value="-1"></el-option>
                        <el-option label="" :value="0"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" :loading="searchLoading" @click="seachSubmit"></el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="centerDialogVisible = true"></el-button>
                </el-form-item>
            </el-form>
        </div>
        <el-table :data="tableData" border stripe height="500" style="width: 100%">
            <el-table-column prop="username" label="">
            </el-table-column>
            <el-table-column prop="department" label="">
            </el-table-column>
            <el-table-column prop="title" label="">
            </el-table-column>
            <el-table-column prop="status" label="">
            </el-table-column>
            <el-table-column prop="createTime" label="">
            </el-table-column>
            <el-table-column fixed="right" label="" width="100">
                <template slot-scope="scope">
                    <el-button type="text" size="small"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination background layout="prev, pager, next" :total="1000" class="pagination-box" @current-change="currentPageFn"></el-pagination>
        <el-dialog title="" :visible.sync="centerDialogVisible" width="50%" center>
            <el-form ref="form" :model="addForm" label-width="80px">
                <el-form-item label="">
                    <el-input v-model="addForm.username"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="addForm.department" placeholder="">
                        <el-option label="" value="shanghai"></el-option>
                        <el-option label="" value="beijing"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="addForm.title" placeholder="">
                        <el-option label="" value="shanghai"></el-option>
                        <el-option label="" value="beijing"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="cancelAddSubmit"> </el-button>
                <el-button type="primary" @click="addSubmit" :loading="addLoading"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
export default {
    name: 'UserManager',
    data () {
        return {
            centerDialogVisible: false,
            searchLoading: false,
            addLoading: false,
            searchForm: {
                username: '',
                department: '',
                title: ''
            },
            addForm: {
                username: '',
                department: '',
                title: ''
            },
            tableData: [{
                username: '',
                department: '',
                title: '',
                status: '',
                createTime: '2016-05-02'
            }, {
                username: '',
                department: '',
                title: '',
                status: '',
                createTime: '2016-05-02'
            }, {
                username: '',
                department: '',
                title: '',
                status: '',
                createTime: '2016-05-02'
            }, {
                username: '',
                department: '',
                title: '',
                status: '',
                createTime: '2016-05-02'
            }, {
                username: '',
                department: '',
                title: '',
                status: '',
                createTime: '2016-05-02'
            }, {
                username: '',
                department: '',
                title: '',
                status: '',
                createTime: '2016-05-02'
            }, {
                username: '',
                department: '',
                title: '',
                status: '',
                createTime: '2016-05-02'
            }, {
                username: '',
                department: '',
                title: '',
                status: '',
                createTime: '2016-05-02'
            }, {
                username: '',
                department: '',
                title: '',
                status: '',
                createTime: '2016-05-02'
            }, {
                username: '',
                department: '',
                title: '',
                status: '',
                createTime: '2016-05-02'
            }, {
                username: '',
                department: '',
                title: '',
                status: '',
                createTime: '2016-05-02'
            }, {
                username: '',
                department: '',
                title: '',
                status: '',
                createTime: '2016-05-02'
            }, {
                username: '',
                department: '',
                title: '',
                status: '',
                createTime: '2016-05-02'
            }, {
                username: '',
                department: '',
                title: '',
                status: '',
                createTime: '2016-05-02'
            }]
        }
    },
    methods: {
        addSubmit () {
            this.addLoading = true
            console.log(this.addForm)
            let that = this
            setTimeout(function () {
                that.addLoading = false
                that.centerDialogVisible = false
            }, 2000)
        },
        cancelAddSubmit () {
            this.centerDialogVisible = false
            this.addForm.username = ''
            this.addForm.department = ''
            this.addForm.title = ''
        },
        seachSubmit () {
            this.searchLoading = true
            console.log(this.searchForm)
            let that = this
            setTimeout(function () {
                that.searchLoading = false
            }, 2000)
        },
        currentPageFn (val) {
            console.log(`${val}`)
        }
    }
}
</script>
<style scoped>
    .userManager{
        width: 100%;
        height: 100%;
    }
</style>



